<template>
    <div id="search">
        <input v-model="keys" type="text" placeholder="请输入关键字">
        <img src="../../assets/sousuo.png" @click="search">
    </div>
</template>

<script>
export default {
    name: 'search',
    props: {
        queryKeys: {
            type: String,
            default: ''
        }
    },
    data () {
        return {
            keys: this.queryKeys
        }
    },
    created () {
        // this.keys = this.queryKes
    },
    methods: {
        search () {
            if (this.keys === '') this.$toast('请输入搜索内容')
            else this.$emit('search', this.keys)
        }
    }
}
</script>

<style lang="scss" scoped>
#search {
  position: relative;
  width: 100%;
  height: 50px;
  margin-left: 20px;
  border-radius: 25px;
  overflow: hidden;
  text-align: left;
  line-height: 1;
  background: #fff;
  input {
    width: 80%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px 10px 10px 24px;
    font-size: 22px;
    color: #666;
    &::placeholder {
        color: #999;
    }
  }
  img{
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
    width: 30px;
  }
}
</style>
