import { NaclHeader, NaclPageContainer, NaclPageContent, NaclIcon, NaclFlexbox } from 'nacl-ui'
import BrandBlock from './components/brand-block'
import BasicFooter from './components/basic-footer'
import moment from 'moment'

class GlobalComponents {
    init (vm) {
        this.vm = vm
        this.registerComponent()
        this.registerFilter()
        this.registerDirective()
    }
    registerComponent () {
        this.vm.component(NaclHeader.name, NaclHeader)
        this.vm.component(NaclPageContainer.name, NaclPageContainer)
        this.vm.component(NaclPageContent.name, NaclPageContent)
        this.vm.component(NaclIcon.name, NaclIcon)
        this.vm.component('nacl-flexbox', NaclFlexbox)
        this.vm.component('nacl-flexbox-item', NaclFlexbox.Item)

        this.vm.component(BrandBlock.name, BrandBlock)
        this.vm.component(BasicFooter.name, BasicFooter)
    }
    registerMixins () {
    }
    registerFilter () {
        // 格式化时间
        this.vm.filter('formatDate', function (value, formatter = 'YYYY-MM-DD HH:mm:ss', isUnix = false) {
            if (!value) {
                return ''
            }
            if (isUnix) {
                return moment.unix(value).format(formatter)
            }
            return moment(value).format(formatter)
        })
    }
    registerDirective () {
    }
}

export default new GlobalComponents()
