<template>
    <div class="basic-input-wrap">
        <div class="basic-input">
            <div class="input-wrap">
                <div :class="['placeholder', { 'is-focus': isFocus }]">{{ placeholder }}</div>
                <nacl-flexbox align="center">
                    <nacl-flexbox-item>
                        <input
                            :value="value"
                            v-bind="$attrs"
                            :type="type"
                            @input="$emit('input', $event.target.value)"
                            @focus="onFocus"
                            @blur="onBlur"
                        >
                    </nacl-flexbox-item>
                    <slot />
                </nacl-flexbox>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'basic-input',
    props: {
        placeholder: {
            type: String,
            required: true
        },
        value: {
            type: String,
            required: true
        },
        type: {
            type: String,
            // required: true,
            default: 'text'
        }
    },
    data () {
        return {
            isFocus: false
        }
    },
    methods: {
        onFocus () {
            this.isFocus = true
        },
        onBlur () {
            if (!this.value) {
                this.isFocus = false
            }
        }
    }
}
</script>

<style scoped lang="scss">
.basic-input-wrap {
  padding: 20px 0;
  border-bottom: 2px solid #999;
}
.basic-input {
  padding-top: 40px;
  .input-wrap {
    position: relative;
    .placeholder {
      position: absolute;
      font-size: 28px;
      color: #999;
      top: 14px;
      left: 2px;
      transition: all 100ms linear;
      pointer-events: none;
      &.is-focus {
        top: -40px;
        left: 0;
        color: #666;
      }
    }
    input {
      width: 100%;
      margin: 10px 0;
      font-size: 36px;
      color: #333;
    }
  }
}
</style>
