<template>
    <div class="box">
        <a @click="goDetails('/'+commodityData.id+'?price='+offerPric)">
            <div class="img"><img :src="imgSrc"></div>
            <nacl-flexbox class="data">
                <p class="title">{{ commodityData.title }}</p>
                <p class="lable">
                    <span>新品上架</span>
                    <span>下单抽奖</span>
                    <span>包邮</span>
                </p>
            </nacl-flexbox>
            <p class="pre">
                <span>¥{{ offerPric || commodityData.price }}</span>
                <span class="origin">¥{{ Math.floor(commodityData.price * 1.5) }}</span>
            </p>
        </a>
    </div>
</template>

<script>

export default {
    name: 'commodity',
    props: {
        offerPric: {
            type: String,
            default: ''
        },
        commodityData: {
            type: Object,
            default () {
                return {}
            }
        }
    },
    data () {
        return {
            imgSrc: require('../../assets/default.jpg')
        }
    },
    mounted () {
        let img = new Image()
        img.onload = () => {
            this.imgSrc = this.commodityData.thumbnail
            img = null
        }
        img.src = this.commodityData.thumbnail
        window.goRouting = this.goRouting;
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        }, 
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goDetails(param){
            this.toGetUser();
            window.webkit.messageHandlers.details.postMessage(param);
        }
    }
}
</script>

<style scoped lang="scss">
.box {
    box-shadow: 0 10px 20px 0 rgba(0, 0, 0, 0.1);
}
.img {
  img {
    display: block;
    width: 90%;
    margin: auto;
    border-radius: 10px;
    margin-top: 15px;
    vertical-align: top;
  }
}
.data {
  padding: 10px 10px;
  box-sizing: border-box;
  align-content: space-between;
  flex-wrap: wrap;
  p {
    width: 100%;
  }
}
.title {
  width: 100%;
  height: 30px;
  font-size: 28px;
  color: #333;
  overflow: hidden; //超出文本隐藏
  line-height: 1.2;
}
.pre {
  padding: 0 10px 10px;
  font-size: 32px;
  color: #ff314a;
  line-height: 1.2;
}
.lable {
  span {
    display: inline-block;
    height: 34px;
    margin-right: 5px;
    padding: 0px 10px;
    border: 1.5px solid #999;
    border-radius: 20px;
    font-size: 22px;
    color: #999;
    line-height: 34px;
  }
}
.origin {
    color: #999;
    font-size: 24px;
    text-decoration: line-through;
    margin-left: 10px;
}
</style>
