<template>
    <nacl-page-container no-header>
        <nacl-page-content
            no-footer no-header
            class="container"
        >
            <!-- <nacl-header
                :fixed="false"
                style="background: transparent; box-shadow: none"
            >
                注册
            </nacl-header> -->
            <div class="content">
                <div class="form">
                    <basic-input
                        v-model="username"
                        placeholder="手机号"
                    />
                    <!-- <basic-input
                        v-model="code"
                        placeholder="验证码"
                    >
                        <nacl-button
                            type="primary"
                            size="sm"
                            radius
                            :disabled="countdown !== 0"
                            :loading="codeBtnLoading"
                            @click="onGetSmsCode"
                        >
                            {{ countdown === 0 ? '获取验证码' : `${countdown}秒后重试` }}
                        </nacl-button>
                    </basic-input> -->
                    <basic-input
                        v-model="password"
                        type="password"
                        placeholder="密码:8-16位字符至少一个字母及数字"
                    />
                    <basic-input
                        v-model="cPassword"
                        type="password"
                        placeholder="重复密码"
                    />
                </div>
                <nacl-button
                    type="primary"
                    radius
                    class="login-btn"
                    :loading="loginBtnLoading"
                    @click="onLogin"
                >
                    注册
                </nacl-button>
                <privacy />
                <div class="to-register">
                    <a @click="goLogin()"
                        replace
                    >
                        已有账号 点击登录
                    </a>
                </div>
            </div>
        </nacl-page-content>
    </nacl-page-container>
</template>

<script>
import BasicInput from '@/components/basic-input'
import Privacy from '@/components/privacy'
import { NaclButton } from 'nacl-ui'
import { registered } from '@/services'

export default {
    name: 'register',
    components: { BasicInput, NaclButton, Privacy },
    data () {
        return {
            countdown: 0,
            username: '',
            code: '',
            cPassword: '',
            password: '',
            codeBtnLoading: false,
            loginBtnLoading: false
        }
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        }, 
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goLogin(){
            this.toGetUser();
            window.webkit.messageHandlers.login.postMessage('');
        },
        startCountdown () {
            this.countdown = 60
            const timer = setInterval(() => {
                this.countdown--
                if (this.countdown <= 0) {
                    clearInterval(timer)
                }
            }, 1000)
        },
        async onLogin () {
            try {
                // console.log(this.password, this.password2)
                this.loginBtnLoading = true
                await registered({
                    username: this.username,
                    password: this.password,
                    cPassword: this.cPassword
                })
                this.$toast('注册成功')
                this.toGetUser();
                window.webkit.messageHandlers.home.postMessage('');
            } catch (e) {
                this.$toast(e.message)
            } finally {
                this.loginBtnLoading = false
            }
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>

<style scoped lang="scss">
.container {
  background: #fff;
}
.content {
  padding:140px 70px 0 70px;
}
h1 {
  position: relative;
  z-index: 2;
  font-size: 60px;
  font-weight: 500;
  color: #333;
  margin-top: 76px;
  &::after {
    content: " ";
    position: absolute;
    z-index: -1;
    left: 0;
    bottom: -10px;
    width: 126px;
    height: 14px;
    border-radius: 5px;
    background: linear-gradient(75deg,#8628fa,#512ffd);
  }
}
.form {
  margin-top: 80px;
}
.login-btn {
  margin-top: 140px;
  margin-bottom: 25px;
}
.to-register {
  font-size: 28px;
  font-weight: 500;
  text-align: center;
  color: #f1302c;
  margin-top: 100px;
}
</style>
