<template>
    <div>
        <ul v-infinite-scroll="loadMore" infinite-scroll-disabled="busy" infinite-scroll-distance="10" class="list">
            <commodityListItem
                v-for="item in listData"
                :key="item.id"
                :offer-pric="offerPric"
                :commodity-data="item"
                class="item"
            />
        </ul>
    </div>
</template>

<script>
import commodityListItem from './commodity-list-item'
import infiniteScroll from 'vue-infinite-scroll'
export default {
    name: 'commodity-list',
    directives: { infiniteScroll },
    components: {
        commodityListItem
    },
    props: {
        offerPric: {
            type: String,
            default: ''
        },
        listData: {
            type: Array,
            default () {
                return []
            }
        },
        noData: {
            type: Boolean,
            default: false
        },
        busy: {
            type: Boolean,
            default: false
        }
    },
    data () {
        return {
        }
    },
    methods: {
        loadMore () {
            // console.log('x')
            this.$emit('loadMore')
        }
    }
}
</script>

<style scoped lang="scss">
.list {
  padding: 0 0px;
  // background: #f7f7f7;
}
.item {
  display: inline-block;
  width: 350px;
  margin: 20px 0 0 16px;
  background: #fff;
  border-radius: 10px;
  overflow: hidden;
}
</style>
