<template>
    <section class="brand-block" :class="{'is-transparent': transparent}" :style="padding">
        <slot />
    </section>
</template>

<script>
export default {
    name: 'brand-block',
    props: {
        horizontal: {
            type: [String, Number],
            default: 15
        },
        vertical: {
            type: [String, Number],
            default: 0
        },
        transparent: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        padding () {
            return {
                padding: `${this.vertical}px ${this.horizontal}px`
            }
        }
    }
}
</script>

<style scoped lang="scss">
    .brand-block {
        background: #fff;
        &.is-transparent {
            background: transparent;
        }
    }
</style>
