export function getComment (id) {
    const localComment = store.get('comment') || []
    const comment = commentData.concat(localComment)
    return comment.filter((item) => item.newsId === id)
}

export function addComment (newsId, user, comment) {
    const localComment = store.get('comment') || []
    const object = {
        newsId,
        user,
        date: moment().format('YYYY.MM.DD'),
        comment
    }
    localComment.push(object)
    store.set('comment', localComment)
}

