/**
 * 添加购物车
 * @return Array<cart>
 */
export async function addCart (query) {
    const response = await request(`${apiPrefix}/wx/cart/add?${queryString.stringify(query)}`, { method: 'POST' })
    if (response.errno === 0) {
        return response.data
    } else {
        let error = { code: -1, message: response.errmsg }
        throw error
    }
}
