export async function getGoodsByKeys (query) {
    const response = await request(`${apiPrefix}/wx/goods/list?${queryString.stringify(query)}`)
    // console.log(response)
    let listData = response.data.list
    for (let i = 0; i < listData.length; i++) {
        // console.log(i)
        let item = listData[i]
        let data = {
            title: item.name,
            link: '',
            price: item.retailPrice,
            comment: '',
            imgSrcArray: item.picUrl,
            thumbnail: item.picUrl,
            id: item.id,
            details: ''
        }
        listData[i] = data
    }
    return listData
}
export async function getGoodsDetail (id) {
    const response = await request(`${apiPrefix}/wx/goods/detail?id=${id}`)
    let item = response.data.info
    let data = {
        title: item.name,
        link: '',
        price: item.retailPrice,
        comment: '',
        imgSrcArray: item.brief.split(','),
        thumbnail: item.picUrl,
        id: item.id,
        details: item.detail
    }
    return data
}
/**
 * 注册
 * @param phoneNumber required
 * @param smsCode required
 * @returns {Promise<User>}
 */
export async function registered (query = {}) {
    if (!query.username) {
        let error = { code: -1, message: '手机号码不能为空' }
        throw error
    }
    if (!PHONE_EXP.test(query.username)) {
        let error = { code: -1, message: '手机号码格式不正确(6到18位字母和数字)' }
        throw error
    }
    if (!query.password) {
        let error = { code: -1, message: '密码不能为空' }
        throw error
    }
    if (query.cPassword !== query.password) {
        let error = { code: -1, message: '两次输入密码不一致' }
        throw error
    }
    if (!PASSWORD_EXP.test(query.password)) {
        let error = { code: -1, message: '密码格式不正确(8-16位字符至少一个字母及数字)' }
        throw error
    }
    const response = await request(`${apiPrefix}/wx/auth/register?username=${query.username}&password=${query.password}&mobile=${query.username}`, {
        method: 'POST'
    })
    console.log(response)

    if (response.errno === 0) {
        UserData = {
            token: response.data.token,
            ...response.data.userInfo
        }
        localStorage.setItem('UserData', JSON.stringify(UserData))
        localStorage.setItem('isShosPrivacy', false)
        localStorage.setItem('isAgreement', false)
        return response.data
    } else {
        console.log(response)
        let error = { code: -1, message: response.errmsg }
        throw error
    }
    // let response = await AV.User.signUpOrlogInWithMobilePhone(phoneNumber, smsCode)
    // return response
}
/**
 * 登陆
 * @param phoneNumber required
 * @param smsCode required
 * @returns {Promise<User>}
 */
export async function login (query = {}) {
    if (!query.username) {
        let error = { code: -1, message: '手机号码不能为空' }
        throw error
    }
    if (!PHONE_EXP.test(query.username)) {
        let error = { code: -1, message: '手机号码格式不正确(6到18位字母和数字)' }
        throw error
    }
    if (!query.password) {
        let error = { code: -1, message: '密码不能为空' }
        throw error
    }
    if (!PASSWORD_EXP.test(query.password)) {
        let error = { code: -1, message: '密码格式不正确(8-16位字符至少一个字母及数字)' }
        throw error
    }
    const response = await request(`${apiPrefix}/wx/auth/login_v1?username=${query.username}&password=${query.password}&mobile=${query.username}`, {
        method: 'POST'
    })
    if (response.errno === 0) {
        UserData = {
            token: response.data.token,
            ...response.data.userInfo
        }
        localStorage.setItem('UserData', JSON.stringify(UserData))
        localStorage.setItem('isShosPrivacy', false)
        localStorage.setItem('isAgreement', false)
        return response.data
    } else {
        console.log(response)
        let error = { code: -1, message: response.errmsg }
        throw error
    }
    // let response = await AV.User.signUpOrlogInWithMobilePhone(phoneNumber, smsCode)
    // return response
}
