/**
 * 获取收藏列表
 * @returns {Promise<User>}
 */
export async function collectList (id) {
    const userData = getUserData()
    const userId = userData.userId
    const params = {
        userId,
        page: 1,
        limit: 30
    }
    const response = await request(`${apiPrefix}/wx/collect/list?${queryString.stringify(params)}`)
    let listData = response.data.list
    for (let i = 0; i < listData.length; i++) {
        // console.log(i)
        let item = listData[i]
        let data = {
            title: item.name,
            link: '',
            price: item.retailPrice,
            comment: '',
            imgSrcArray: item.picUrl,
            thumbnail: item.picUrl,
            id: item.id,
            details: '',
            valueId: item.valueId
        }
        listData[i] = data
    }
    if (id) {
        let data = listData.filter((item) => item.valueId === id)
        return data.length !== 0 ? data[0] : null
    } else return listData
}

