import Vue from 'vue'
import AV from 'leancloud-storage'
import Toast from 'vue2-toast'
import 'vue2-toast/lib/toast.css'

import App from './App.vue'
import router from './router'
import store from './store'
import VueGlobal from './vue-global'
import './styles/index.scss'
import './registerServiceWorker'
import config from './config'

router.beforeEach ((to, from, next) => {
    //这里加入set方法
    window.onload  = () => {
        window.setUser = (data) => {
            // console.log(data)
            localStorage.clear();
            let datas = JSON.parse(decodeURIComponent(data));
            for(let key in datas){
                localStorage.setItem(key,datas[key])
            }
            // console.log(localStorage)
            setTimeout(function(){next()}, 10)
        }
    }
})


Vue.config.productionTip = false
VueGlobal.init(Vue)

Vue.use(Toast, {
    type: 'center',
    duration: 3000,
    wordWrap: true
})

AV.init({
    appId: config.leanCloudKey,
    appKey: config.leanCloudSecret,
    serverURLs: config.serverURLs
})

new Vue({
    router,
    store,
    render: (h) => h(App)
}).$mount('#app')

Vue.filter('getImgSrc', (val, size) => {
    let imgSrc = val.split(',')[0]
    return imgSrc.replace('x', size)
})
