<script>
import 'swiper/dist/css/swiper.css'
import search from '../components/search'
import commodity from '../components/commodity'
import commodityList from '../components/commodity/commodity-list'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import AV from 'leancloud-storage'
import { getGoodsByKeys } from '@/services'
import privacy from '@/components/privacy-alter'
import agreement from '@/components/agreement-alter'

export default {
    name: 'home',
    components: {
        swiper,
        swiperSlide,
        commodity,
        search,
        commodityList,
        privacy,
        agreement
    },
    data () {
        return {
            keysArray: ['T恤', '帆布', '连衣裙', '针织衫', '皮衣'],
            listData: [],
            offerData: [],
            activeTypeKeys: null,
            typeDada: [
                {
                    title: '推荐',
                    keys: null
                },
                {
                    title: '牛仔裤',
                    keys: '牛仔裤'
                },
                {
                    title: '针织衫',
                    keys: '针织衫'
                },
                {
                    title: '打底裤',
                    keys: '打底裤'
                },
                {
                    title: 'T恤',
                    keys: 'T恤'
                },
                {
                    title: '皮衣',
                    keys: '皮衣'
                }
            ],
            countdown: {
                h: '00',
                m: '00',
                s: '00'
            }
        }
    },
    computed: {
        username () {
            if (AV.User.current()) return AV.User.current().get('username')
            else return null
        },
        isShowRecord () {
            return !this.$route.query.channel
        }
    },
    created () {
        this.getList(null)
        // this.getSpike()
        this.setCountdown()
    },
    methods: {
        goRouting(param){
            this.toGetUser();
            window.webkit.messageHandlers.goRouting.postMessage(param);
        },
        toGetUser(){
            const cutCount = Math.ceil(JSON.stringify(localStorage).length / 1000);
            for(let i = 0 ; i<cutCount;i++){
                if(i!=cutCount-1){
                    if(i==0){
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }else{
                        window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,1000*(i+1))));
                    }
                    
                }else{
                    window.webkit.messageHandlers.getUser.postMessage(i+"@_@"+encodeURIComponent(JSON.stringify(localStorage).substring(1000*i,JSON.stringify(localStorage).length)));
                }
            }
        },
        goTimeDraw(param){
            this.toGetUser();
            window.webkit.messageHandlers.timeDraw.postMessage(param);
        },
        goOrderLottery(){
            this.toGetUser();
            window.webkit.messageHandlers.orderLottery.postMessage('');
        },
        goCommodityList(param){
            this.toGetUser();
            window.webkit.messageHandlers.commodityList.postMessage(param);
        },
        options () {
            // const _this = this
            const options = {
                autoplay: true,
                slidesPerView: 1,
                spaceBetween: 0,
                centeredSlides: true,
                loopAdditionalSlides: 1,
                loop: false,
                on: {
                    click: function () {
                        // if (this.realIndex === 0) {
                        //     _this.$router.push({ path: '/commodity-list/包包 女' })
                        // } else if (this.realIndex === 1) {
                        //     _this.$router.push({ path: '/commodity-list/帆布鞋 女' })
                        // } else if (this.realIndex === 2) {
                        //     _this.$router.push({ path: '/commodity-list/针织衫' })
                        // }
                    }
                }
            }
            return options
        },
        async getList (keys) {
            const time = new Date()
            const index = time.getTime() % 6
            const params = {
                pageIndex: 1,
                pageSize: 20,
                keyword: keys || this.keysArray[index]
            }
            this.listData = await getGoodsByKeys(params)
            if (this.offerData.length === 0) this.offerData = this.listData.slice(-3)
        },
        search (keys) {
            this.$router.push({
                name: 'search',
                query: {
                    keys
                }
            })
        },
        clickType (keys) {
            this.activeTypeKeys = keys
            // this.listData = []
            this.getList(keys)
        },
        setCountdown () {
            let timestamp = new Date().getTime()
            timestamp = timestamp + 1 * 60 * 60 * 1000
            let dateAfter = new Date(timestamp)
            let endTime = dateAfter.getFullYear() + '-' + (dateAfter.getMonth() + 1) + '-' + dateAfter.getDate() + ' ' + dateAfter.getHours() + ':00'
            // 目标日期时间戳
            const end = Date.parse(new Date(endTime))
            // 当前时间戳
            const now = Date.parse(new Date())
            // 相差的毫秒数
            const msec = end - now
            // 计算时分秒数
            // let day = parseInt(msec / 1000 / 60 / 60 / 24)
            let hr = parseInt(msec / 1000 / 60 / 60 % 24)
            let min = parseInt(msec / 1000 / 60 % 60)
            let sec = parseInt(msec / 1000 % 60)
            // 个位数前补零
            hr = hr > 9 ? hr : '0' + hr
            min = min > 9 ? min : '0' + min
            sec = sec > 9 ? sec : '0' + sec
            this.countdown = {
                h: hr,
                m: min,
                s: sec
            }
            setTimeout(() => this.setCountdown(), 1000)
        },
        openDiscount (title) {
            const time = new Date()
            const index = time.getTime() % 8
            // this.$router.push(`/commodity-list/${this.keysArray[index]}?title=${title}`)
            this.toGetUser();
            window.webkit.messageHandlers.commodityList.postMessage('/'+this.keysArray[index]+'?title='+title);
        }
    },
    mounted(){
        window.goRouting = this.goRouting;
    }
}
</script>
